import React, { Component } from "react";
import { bootstrapSnippet, SnippetEditor } from "./SnippetEditor";
import { Modal } from "./Modal";
import { Escapable } from "./Escapable";

export class CreateSnippet extends Component {
  constructor() {
    super();

    this.state = {
      isAdding: false,
      value: ""
    };

    this.onStart = this.onStart.bind(this);
    this.onSave = this.onSave.bind(this);
    this.onClose = this.onClose.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  onStart(e) {
    this.setState({ isAdding: true });
  }

  onSave(snippet) {
    this.props.createSnippet(snippet);

    this.close();
  }

  onClose() {
    this.close();
  }

  close() {
    this.setState({ isAdding: false, value: "" });
  }

  onChange(e) {
    this.setState({ isAdding: true, value: e.target.value });
  }

  render() {
    const presentationMode = "inline";

    const snippet = bootstrapSnippet("", "", this.state.value);

    const modalPresentation = (
      <Modal onClose={this.onClose}>
        <SnippetEditor onSave={this.onSave} snippet={snippet} />
      </Modal>
    );

    const inlinePresentation = (
      <Escapable onEscape={this.onClose}>
        <SnippetEditor onSave={this.onSave} snippet={snippet} />
        <button onClick={this.onClose}>cancel</button>
      </Escapable>
    );

    const modeToPresentation = {
      inline: inlinePresentation,
      modal: modalPresentation
    };

    const inputSection = (
      <div className="createSnippetInputSection">
        <input
          placeholder="New Snippet..."
          onChange={this.onChange}
          value={this.state.value}
          tabIndex="1"
        />
      </div>
    );

    return (
      <div>
        {this.state.isAdding
          ? modeToPresentation[presentationMode]
          : inputSection}
      </div>
    );
  }
}



