const fileExtensionToLanguage = {
  js: "javascript",
  html: "html"
};

export function detectLanguage(fileName) {
  return fileExtensionToLanguage[fileName.split(".").pop() || "js"];
}

export function detectAndSetLanguages(snippet) {
  const files = snippet.files.map(file => {
    const language = detectLanguage(file.name);

    if (language) {
      file.language = language;
    }

    return file;
  });

  return { ...snippet, files };
}

export function searchSnippet(snippet, query) {
  const upperCaseQuery = query.toUpperCase();

  return []
    .concat(snippet.files.map(file => file.name))
    .concat(snippet.files.map(file => file.content))
    .concat(snippet.tags)
    .concat(snippet.title)
    .some(text => text.toUpperCase().includes(upperCaseQuery));
}

export function replaceItemInListById(list, id, newItem) {
  return list.map(item => (item.id === id ? newItem : item));
}

export function dropItemInListById(list, id) {
  return list.filter(item => item.id !== id);
}

export function aggregateTags(snippets) {
  const tags = snippets.reduce((acc, snippet) => acc.concat(snippet.tags), []);

  return [...new Set(tags)];
}

export function copyToClipboard(str) {
  const el = document.createElement("textarea");
  el.value = str;
  document.body.appendChild(el);
  el.select();
  document.execCommand("copy");
  document.body.removeChild(el);
}



