import React, { Component } from "react";
import { CodeMirrorWrapper } from "./CodeMirrorWrapper";
import { EditableLabel } from "./EditableLabel";
import { detectLanguage } from "./helpers";

export function bootstrapSnippet(title, filename, content) {
  return {
    title: title,
    files: [
      {
        name: filename,
        content: content,
        language: detectLanguage(filename)
      }
    ],
    tags: []
  };
}

export class SnippetEditor extends Component {
  constructor(props) {
    super(props);

    this.onSave = this.onSave.bind(this);
    this.onAddFile = this.onAddFile.bind(this);
    this.onChangeTitle = this.onChangeTitle.bind(this);
    this.onKeyPressInAddTag = this.onKeyPressInAddTag.bind(this);
    this.onAddTag = this.onAddTag.bind(this);
    this.onChangeTagInput = this.onChangeTagInput.bind(this);

    this.codeMirrorWrappers = [];

    this.state = {
      snippet:
        props.snippet ||
        bootstrapSnippet("New", "new.js", "console.log('hello world');"),
      currentFileIdx: 0,
      tagInput: ""
    };
  }

  componentDidMount() {
    this.focusActiveEditor(this.state.currentFileIdx);
  }

  focusActiveEditor(index) {
    this.codeMirrorWrappers.forEach(
      (cm, _index) => (index === _index ? cm.focus() : undefined)
    );
  }

  onChangeTitle(title) {
    this.setState(oldState => {
      return {
        snippet: {
          ...oldState.snippet,
          title
        }
      };
    });
  }

  onChangeContent(index, cm) {
    const content = cm.getValue();

    this.setState(oldState => {
      const files = oldState.snippet.files.map(
        (file, _index) => (index === _index ? { ...file, content } : file)
      );

      return { snippet: { ...oldState.snippet, files } };
    });
  }

  onChangeFileName(index, newName) {
    this.setState(oldState => {
      const files = oldState.snippet.files.map(
        (file, _index) =>
          index === _index
            ? { ...file, name: newName, language: detectLanguage(newName) }
            : file
      );

      return { snippet: { ...oldState.snippet, files } };
    });
  }

  onAddFile(e) {
    this.setState(oldState => {
      const files = oldState.snippet.files.concat({
        name: "file.txt",
        content: "b"
      });

      return {
        snippet: { ...oldState.snippet, files },
        currentFileIdx: files.length - 1
      };
    });
  }

  onSelectFile(index) {
    this.setState({
      currentFileIdx: index
    });

    this.focusActiveEditor(index);
  }

  onDeleteFile(index) {
    this.setState(oldState => {
      const files = oldState.snippet.files.filter(
        (file, _index) => index !== _index
      );

      const currentFileIdx = 0;

      return {
        snippet: { ...oldState.snippet, files },
        currentFileIdx
      };
    });
  }

  onKeyPressInAddTag(e) {
    if (e.key === "Enter") {
      this.onAddTag();
    }
  }

  onAddTag() {
    this.setState(oldState => {
      const tags = oldState.snippet.tags.concat(this.state.tagInput);

      return {
        tagInput: "",
        snippet: { ...oldState.snippet, tags }
      };
    });
  }

  onDeleteTag(index) {
    this.setState(oldState => {
      const tags = oldState.snippet.tags.filter(
        (tag, _index) => index !== _index
      );

      return {
        snippet: { ...oldState.snippet, tags }
      };
    });
  }

  onChangeTagInput(e) {
    this.setState({ tagInput: e.target.value });
  }

  onSave(e) {
    this.props.onSave({
      ...this.state.snippet,
      files: this.state.snippet.files.map((file, index) => {
        return {
          ...file,
          content: this.codeMirrorWrappers[index].getValue()
        };
      })
    });
  }

  render() {
    const fileEditors = this.state.snippet.files.map((file, index) => (
      <CodeMirrorWrapper
        hide={this.state.currentFileIdx !== index}
        language={file.language}
        key={`${file.name}${index}`}
        ref={ref => (this.codeMirrorWrappers[index] = ref)}
        value={file.content}
        onChange={this.onChangeContent.bind(this, index)}
      />
    ));

    const fileTabs = this.state.snippet.files.map((file, index) => (
      <span key={`${file.name}${index}`}>
        <span
          style={{ paddingLeft: "10px", userSelect: "none" }}
          onClick={this.onSelectFile.bind(this, index)}
        >
          <EditableLabel
            value={file.name}
            onChange={this.onChangeFileName.bind(this, index)}
          />
        </span>

        <button onClick={this.onDeleteFile.bind(this, index)}>x</button>
      </span>
    ));

    const tags = this.state.snippet.tags.map((tag, index) => (
      <span key={index} style={{ marginLeft: "10px" }}>
        {tag}
        <button onClick={this.onDeleteTag.bind(this, index)}>x</button>
      </span>
    ));

    return (
      <div>
        <div>
          <EditableLabel
            value={this.state.snippet.title}
            onChange={this.onChangeTitle}
          />

          {fileTabs}

          <button onClick={this.onAddFile}>add file</button>

          {fileEditors}

          <br />

          {tags}

          <input
            value={this.state.tagInput}
            placeholder="add tag"
            onChange={this.onChangeTagInput}
            onKeyPress={this.onKeyPressInAddTag}
          />

          <button onClick={this.onAddTag}>add tag</button>

          <br />

          <button onClick={this.onSave}>save</button>
        </div>
      </div>
    );
  }
}



