import React, { Component } from "react";
import { Modal } from "./Modal";
import { searchSnippet, copyToClipboard } from "./helpers";
import { CodeBlock } from "./CodeBlock";

class Snippet extends Component {
  render() {
    const files = this.props.snippet.files.map((file, index) => (
      <CodeBlock key={index} language={file.language} content={file.content} />
    ));

    return (
      <div>
        <div>{this.props.snippet.title}</div>
        {files}
      </div>
    );
  }
}

export class QuickSearch extends Component {
  constructor(props) {
    super(props);

    this.onQueryChange = this.onQueryChange.bind(this);
    this.onKeyDown = this.onKeyDown.bind(this);

    this.state = { filteredSnippets: [], query: "", selectedSnippetIndex: 0 };
  }

  onQueryChange(e) {
    const query = e.target.value;
    this.setState({
      query,
      filteredSnippets: this.props.snippets.filter(snippet =>
        searchSnippet(snippet, query)
      )
    });
  }

  onKeyDown(e) {
    if (e.keyCode === 38) {
      // up arrow
      let idx = this.state.selectedSnippetIndex;
      e.preventDefault();
      idx--;
      if (idx < 0) {
        idx = this.state.filteredSnippets.length - 1;
      }
      this.setState({ selectedSnippetIndex: idx });
    } else if (e.keyCode === 40) {
      // down arrow
      let idx = this.state.selectedSnippetIndex;
      e.preventDefault();
      idx++;
      if (idx >= this.state.filteredSnippets.length) {
        idx = 0;
      }
      this.setState({ selectedSnippetIndex: idx });
    } else if (e.keyCode === 13) {
      // enter
      e.preventDefault();
      const content = this.state.filteredSnippets[
        this.state.selectedSnippetIndex
      ].files[0].content;

      copyToClipboard(content);

      this.props.onClose();
    }
  }

  render() {
    const style = index =>
      this.state.selectedSnippetIndex === index
        ? {
            border: "solid 1px black"
          }
        : undefined;

    const filteredSnippets =
      this.state.query !== "" &&
      this.state.filteredSnippets.map((snippet, index) => (
        <div key={snippet.id} style={style(index)}>
          <Snippet snippet={snippet} />
        </div>
      ));

    return (
      <Modal onClose={this.props.onClose}>
        <input
          autoFocus
          onChange={this.onQueryChange}
          onKeyDown={this.onKeyDown}
        />

        {filteredSnippets}
      </Modal>
    );
  }
}



