import React, { Component } from "react";
import { CodeBlock } from "./CodeBlock";

export class Snippet extends Component {
  render() {
    const firstFile = this.props.snippet.files[0];

    const codeBlock = firstFile && (
      <CodeBlock language={firstFile.language} content={firstFile.content} />
    );

    return (
      <div className="snippetOuter">
        <div className="snippetTitle">{this.props.snippet.title}</div>
        {codeBlock}
      </div>
    );
  }
}

const loadingStyle = {
  position: "absolute",
  top: "0px",
  right: "0px",
  bottom: "0px",
  left: "0px",
  backgroundColor: "white",
  opacity: "0.7"
};

const loadingIndicator = (snippet, currentlyLoadingSnippetId) =>
  currentlyLoadingSnippetId === snippet.id && <div style={loadingStyle} />;

export class SnippetLayout extends Component {
  onClickSnippet(snippet) {
    this.props.onClickSnippet(snippet);
  }

  onClickDelete(snippet) {
    this.props.onClickDelete(snippet);
  }

  render() {
    return this.props.snippets.map(snippet => (
      <div className="snippetOuterContainer" key={snippet.id}>
        <div
          className="snippetInnerContainer"
          onClick={this.onClickSnippet.bind(this, snippet)}
        >
          {loadingIndicator(snippet, this.props.currentlyLoadingSnippetId)}

          <Snippet snippet={snippet} />
        </div>

        <button
          style={{
            float: "right",
            position: "relative",
            top: "-17px",
            right: "-1px"
          }}
          onClick={this.onClickDelete.bind(this, snippet)}
        >
          x
        </button>
      </div>
    ));
  }
}



