export class Backend {
  constructor() {
    this.db = window.firebase.firestore();
    this.db.settings({ timestampsInSnapshots: true });
  }

  loadAllSnippets() {
    return new Promise((resolve, reject) => {
      this.db
        .collection("snippets")
        .get()
        .then(result => {
          const snippets = [];
          result.forEach(doc => snippets.push({ ...doc.data(), id: doc.id }));

          resolve(snippets);
        });
    });
  }

  createSnippet(snippet) {
    return new Promise((resolve, reject) => {
      this.db
        .collection("snippets")
        .add(snippet)
        .then(doc => {
          resolve({ ...snippet, id: doc.id });
        });
    });
  }

  updateSnippet(snippet) {
    return new Promise((resolve, reject) => {
      this.db
        .collection("snippets")
        .doc(snippet.id)
        .set(snippet)
        .then(() => {
          resolve(snippet);
        });
    });
  }

  deleteSnippet(snippet) {
    return new Promise((resolve, reject) => {
      this.db
        .collection("snippets")
        .doc(snippet.id)
        .delete()
        .then(() => {
          resolve(snippet);
        });
    });
  }
}



