import React, { Component } from "react";
import "./App.css";
import { CreateSnippet } from "./CreateSnippet";
import { SnippetEditor } from "./SnippetEditor";
import { Modal } from "./Modal";
import { Sidebar } from "./Sidebar";
import { Mainbar } from "./Mainbar";
import { Header } from "./Header";
import { Content } from "./Content";
import { QuickSearch } from "./QuickSearch";
import { SnippetLayout } from "./SnippetLayout";
import {
  aggregateTags,
  detectAndSetLanguages,
  replaceItemInListById,
  searchSnippet,
  dropItemInListById
} from "./helpers";
import { Backend } from "./Backend";

const initialState = {
  currentlyEditedSnippetId: null,
  currentlyLoadingSnippetId: null,
  snippets: [],
  tags: [],
  query: "",
  isDoingQuickSearch: false,
  layout: 1
};

class App extends Component {
  constructor() {
    super();

    this.state = initialState;
    this.createSnippet = this.createSnippet.bind(this);
    this.stopEdit = this.stopEdit.bind(this);
    this.updateSnippet = this.updateSnippet.bind(this);
    this.onChangeQuery = this.onChangeQuery.bind(this);
    this.handleGlobalKeydown = this.handleGlobalKeydown.bind(this);
    this.handleGlobalKeyup = this.handleGlobalKeyup.bind(this);
    this.closeQuickSearch = this.closeQuickSearch.bind(this);

    document.addEventListener("keydown", this.handleGlobalKeydown);
    document.addEventListener("keyup", this.handleGlobalKeyup);

    this.cmdDown = false;

    this.backend = new Backend();

    this.backend
      .loadAllSnippets()
      .then(snippets =>
        this.setState({ snippets, tags: aggregateTags(snippets) })
      );
  }

  createSnippet(snippet) {
    snippet = detectAndSetLanguages(snippet);

    this.setState({
      snippets: this.state.snippets.concat({ ...snippet, id: "new" }),
      currentlyLoadingSnippetId: "new"
    });

    this.backend.createSnippet(snippet).then(newSnippet => {
      this.setState(oldState => {
        const snippets = replaceItemInListById(
          oldState.snippets,
          "new",
          newSnippet
        );

        return {
          snippets,
          tags: aggregateTags(snippets),
          currentlyLoadingSnippetId: null
        };
      });
    });
  }

  updateSnippet(snippet) {
    snippet = detectAndSetLanguages(snippet);

    this.setState({ currentlyLoadingSnippetId: snippet.id });

    this.backend.updateSnippet(snippet).then(updatedSnippet => {
      this.setState(oldState => {
        const snippets = replaceItemInListById(
          oldState.snippets,
          updatedSnippet.id,
          updatedSnippet
        );

        return {
          snippets,
          tags: aggregateTags(snippets),
          currentlyLoadingSnippetId: null
        };
      });
    });

    this.stopEdit();
  }

  deleteSnippet(snippet) {
    this.setState({ currentlyLoadingSnippetId: snippet.id });

    this.backend.deleteSnippet(snippet).then(deletedSnippet => {
      this.setState(oldState => {
        const snippets = dropItemInListById(
          oldState.snippets,
          deletedSnippet.id
        );

        return {
          snippets,
          tags: aggregateTags(snippets),
          currentlyLoadingSnippetId: null
        };
      });
    });
  }

  startEdit(snippet) {
    this.setState({
      currentlyEditedSnippetId: snippet.id
    });
  }

  stopEdit() {
    this.setState({
      currentlyEditedSnippetId: null
    });
  }

  getCurrentlyEditedSnippet() {
    const index = this.state.snippets.findIndex(
      snippet => snippet.id === this.state.currentlyEditedSnippetId
    );
    return this.state.snippets[index];
  }

  onChangeQuery(e) {
    this.setState({ query: e.target.value });
  }

  handleGlobalKeydown(e) {
    if (e.keyCode === 91) {
      this.cmdDown = true;
    } else if (
      e.keyCode === 75 &&
      e.repeat === false &&
      this.cmdDown === true
    ) {
      this.setState({ isDoingQuickSearch: true });
    }
  }

  handleGlobalKeyup(e) {
    if (e.keyCode === 91) {
      this.cmdDown = false;
    }
  }

  closeQuickSearch() {
    this.setState({ isDoingQuickSearch: false });
  }

  setLayout(layout) {
    this.setState({ layout });
  }

  render() {
    const currentlyEditedSnippedModal = this.state.currentlyEditedSnippetId !==
      null && (
      <Modal onClose={this.stopEdit}>
        <SnippetEditor
          snippet={this.getCurrentlyEditedSnippet()}
          onSave={this.updateSnippet}
        />

        <button onClick={this.stopEdit}>cancel</button>
      </Modal>
    );

    const filteredSnippets = this.state.snippets.filter(snippet =>
      searchSnippet(snippet, this.state.query)
    );

    const layout = (
      <SnippetLayout
        snippets={filteredSnippets}
        onClickSnippet={this.startEdit.bind(this)}
        onClickDelete={this.deleteSnippet.bind(this)}
        currentlyLoadingSnippetId={this.state.currentlyLoadingSnippetId}
      />
    );

    const layoutClass = this.state.layout === 0 ? "tile-layout" : "list-layout";

    const quickSearch = this.state.isDoingQuickSearch && (
      <QuickSearch
        snippets={this.state.snippets}
        onClose={this.closeQuickSearch}
      />
    );

    return (
      <div className={`main ${layoutClass}`}>
        <Sidebar tags={this.state.tags} />

        <Mainbar>
          <Header />

          <Content>
            <input onChange={this.onChangeQuery} value={this.query} />

            <button onClick={this.setLayout.bind(this, 0)}>tile</button>
            <button onClick={this.setLayout.bind(this, 1)}>list</button>

            <CreateSnippet createSnippet={this.createSnippet} />

            {currentlyEditedSnippedModal}

            {layout}

            {quickSearch}
          </Content>
        </Mainbar>
      </div>
    );
  }
}

export default App;



