import React, { Component } from "react";

export class EditableLabel extends Component {
  constructor(props) {
    super(props);

    this.state = { isEditing: false, value: props.value };

    this.onStartEdit = this.onStartEdit.bind(this);
    this.onStopEdit = this.onStopEdit.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onKeyPress = this.onKeyPress.bind(this);
  }

  onStartEdit() {
    this.setState({ isEditing: true });
  }

  onStopEdit() {
    this.setState({ isEditing: false });

    this.props.onChange(this.state.value);
  }

  onChange($event) {
    const value = $event.target.value;

    this.setState({ value });
  }

  onKeyPress(e) {
    if (e.key === "Enter") {
      this.onStopEdit();
    }
  }

  render() {
    const input = (
      <input
        value={this.state.value}
        onChange={this.onChange}
        onBlur={this.onStopEdit}
        onKeyPress={this.onKeyPress}
        autoFocus
      />
    );

    const span = (
      <span onDoubleClick={this.onStartEdit}>{this.state.value}</span>
    );

    return this.state.isEditing ? input : span;
  }
}



