import React, { Component } from "react";

import * as CodeMirror from "codemirror/lib/codemirror";
import "codemirror/mode/htmlmixed/htmlmixed";
import "codemirror/mode/javascript/javascript";
import "codemirror/lib/codemirror.css";
import "./codemirror-prism-okaidia.css";

const languageToMode = {
  html: "htmlmixed"
};

export class CodeMirrorWrapper extends Component {
  constructor(props) {
    super(props);

    this.language = this.props.language;
  }

  componentDidMount() {
    this.initialize();
  }

  componentWillUnmount() {
    this.cm.toTextArea();
  }

  initialize() {
    this.cm = CodeMirror.fromTextArea(this.textarea, {
      lineNumbers: true,
      mode: languageToMode[this.language] || this.language,
      theme: "prism-okaidia"
    });

    this.setValue(this.props.value);

    this.cm.on("change", this.props.onChange || (() => void 0));

    this.focus();
  }

  getValue() {
    return this.cm.getValue();
  }

  setValue(value) {
    this.cm.setValue(value);
  }

  setLanguage(language) {
    this.language = language;

    this.cm.toTextArea();
    this.initialize();
  }

  focus() {
    this.cm.focus();
    this.cm.execCommand("goDocEnd");
  }

  render() {
    const style = {
      position: "absolute",
      top: "-10000px",
      left: "-10000px"
    };

    return (
      <div style={this.props.hide ? style : {}}>
        <textarea ref={el => (this.textarea = el)} />
      </div>
    );
  }
}



